<template>
    <div class="login container">
        <form  @submit.prevent="login"  class="card-panel">
            <h2 class="center deep-purple-text">HighGo 管理者登入</h2>
            <div class="field">
                <label for="name">帳號:</label>
                <input type="text" name="name" v-model="name">
            </div>
            <div class="field">
                <label for="password">密碼:</label>
                <input type="password" name="password"  v-model="password">
            </div>
            <p v-if="feedback" class="red-text center">{{feedback}}</p>
            <div class="field center">
                <button class="btn deep-purple">確定</button>
            </div>
        </form>
    </div>
</template>


<script>
 import  {userchange}  from  '@/router/user.js'
export default {
    name:'Login',
    data(){
        return {
            name:null,
            password:null,
            name_corrent:'BoxChen',
            password_corrent:'123456',
            feedback:null
        }
    },
    methods:{
        login(){
            if(this.name && this.password){
                if(this.name == this.name_corrent && this.password == this.password_corrent)
                {
                     userchange(1);
                     this.$router.push({name:'GMap'})
                     this.feedback = null;
                }
                else
                {
                    this.feedback = "帳號密碼錯誤";
                }
            }
            else{
                this.feedback = "請填入帳號密碼";
            }
        }
    }
}
</script>

<style>
    .login{
        max-width:400px;
        margin-top: 200px;
    }
    .login h2{
        font-size:2.1em;
        margin-bottom: 50px;
        margin-top: 30px;
    }
    .login .field label{
        font-size:1.3em;
        text-align: center;
    }
    .login .field{
        margin-bottom:16px;
        max-width:     280px;
        margin:        auto;
    }
    .login .field.field.center{
    margin-top: 40px;
    }
    .login .btn{
    width:     180px;
    height:    40px;
    font-size: 1.25em;
    }
</style>


