<template>
    <div  v-if="showModal_AddCar" @close="showModal_AddCar = false">
            <transition name="modal">
                <div class="modal-mask">
                <div class="modal-wrapper">
                    <div class="modal-container">

                    <div class="modal-header">
                        <button class="modal-close-button" @click="Switch_CarDetail()">
                            X
                        </button>
                    </div>

                    <div class="modal-body">
                      <!-- Img -->
                      <div class="body_Img">
                          <div>
                              <div class="body_ImgView_Slider_Pos"  v-bind:class="{'body_ImgView_Slider':isSlide}" >
                                      <div class="body_ImgView_Frame" v-if="this.ImgSelectViewA >=0  && ImgView_cache[ImgSelectViewA]!=null"      @mouseover="ImgBtnShow =1" @mouseleave="ImgBtnShow =0">
                                          <img  class="View_Img_Btn delectpos"  :src="ViewImgBtnSrc_delect"  v-bind:style="{ opacity:ImgBtnsShow,transition: transitionTime}" @mousedown="ViewImgBtnSrc_delect = require('../../assets/delect_click.png');" @mouseup="ViewImgBtnSrc_delect = require('../../assets/delect.png');BtnSrc_deleteFun();"/> 
                                           <img  class="View_Img_Btn Mainpos"  :src="ViewImgBtnSrc_Main"  v-bind:style="{ opacity:ImgBtnsShow,transition: transitionTime}" @mousedown="ViewImgBtnSrc_Main = require('../../assets/isMainBG_click.png');" @mouseup="ViewImgBtnSrc_Main = require('../../assets/isMainBG.png');BtnSrc_MainFun();"/> 
                                          <img  class="body_View_Img"  :src="ImgView_cache[ImgSelectViewA].URL" >  
                                      </div>
                                      
                                      <div class="body_ImgView_Frame_2" v-if="this.ImgSelectViewB >=0 && ImgView_cache[ImgSelectViewB]!=null" >
                                          <img  class="body_View_Img"  :src="ImgView_cache[ImgSelectViewB].URL"  >  
                                      </div>
                              </div>
                          </div>
                          
                          <div class="body_ImgSelect">
                              <!-- Photos Area -->
                                <div  v-if="ImgView_cache.length >0" 　　class="body_ImgSelect_Now"  v-bind:style="{ left: SelectImg_BlockPosLeft,transition: transitionTime }" />
                                    <div  class="body_ImgSelect_inner" v-bind:style="{ width: Img_inner_width, left: Img_inner_XOff,transition: transitionTime}">
                                        <div  class="body_ImgSelect_innerImg" v-for="(photo,index) in ImgView_cache" :key="index">
                                            <div  v-on:click="ClickCarPhotoSelect(index)">
                                                  <img  class="body_ImgSelect_Img" :src="photo.URL" >  
                                            </div>
                                        </div>
                                    </div>
                                  <!-- Photos Arrow Area -->  
                                 <div  v-if="this.CarDatailAdd.photos.length >5" class="body_ImgSelect_Arrow ArrayLeft" @click="Click_Arrow_Left()">
                                      <img class="ArrowImg"  src="../../assets/arrow.png" >  
                                   </div>
                                 <div v-if="this.CarDatailAdd.photos.length >5" class="body_ImgSelect_Arrow ArrayRight" @click="Click_Arrow_Right()">
                                      <img class="ArrowImg ArrowImf_Filp"  src="../../assets/arrow.png" > 
                                   </div>
                          </div>
                      
                          <!-- uploadImg -->
                          <div class="fileBtnDiv">
                            <input type="file" id="file" accept="image/*" @change="UploadImg">
                            <label for="file">
                              <i class="material-icons">
                                    add_photo_alternate
                              </i>
                              選擇照片
                            </label>
                          </div>

                      </div>
                      <!-- Data -->
                          <table  border="0" cellpadding="0" cellspacing="0" style="padding:0;margin-top:-10px;">
                              <tbody>
                                    <td align="center" valign="top" style="padding-left:0;padding-right:0;">
                                      <table width="100%" border="1" cellpadding="1" cellspacing="1">
                                          <tbody>
                                              <tr>
                                                  <td class="css_CarTitle">車型</td>
                                                  <td class="css_CarContent" colspan="3"><input v-model="type_change" value="type"  v-on:input="CheckType(type,type_change)"></td>
                                             </tr>
                                              <tr>
                                                  <td class="css_CarTitle">車牌</td>
                                                  <td  class="css_CarContent"  colspan="3"><input v-model="plate_change" value="plate" v-on:input="CheckType(plate,plate_change)"></td>
                                              </tr>
                                              <tr>
                                                  <td class="css_CarTitle">排氣量</td>
                                                  <td class="css_CarContent"><input  v-model="cc_change" value="cc" v-on:input="CheckType(cc,cc_change)"></td>                                            
                                                  <td class="css_CarTitle">容納人數</td>
                                                  <td class="css_CarContent" ><input v-model="capacity_change" value="capacity" v-on:input="CheckType(capacity,capacity_change)"></td>
                                              </tr>
                                              <tr>
                                                  <td class="css_CarTitle">平日租金</td>
                                                  <td class="css_CarContent"><input v-model="fee_nor_change" value="fee_nor"  v-on:input="CheckType(fee_nor,fee_nor_change)"></td>
                                                  <td class="css_CarTitle">假日租金</td>
                                                  <td class="css_CarContent"><input v-model="fee_holi_change" value="fee_holi" v-on:input="CheckType(fee_holi,fee_holi_change)"></td>
                                              </tr>
                                              <tr>                                                                                              <tr>
                                                  <td class="css_CarTitle">目前位置</td>
                                                  <td class="css_CarContent" colspan="3"><input v-model="location_change" value="location" v-on:input="CheckType(location,fee_holi_change)"> </td>
                                              </tr>
                                          </tbody>
                                      </table>
                                    </td>

                              </tbody>
                          </table>
                    </div>


                    <div class="modal-footer">
                        <button class="modal-default-button"  v-bind:class="{'btn_show':isFixBtnhide}"  @click="AddData()">
                            新增
                        </button>
                    </div>
                        {{testinfo}}

                    </div>
                </div>
                </div>
            </transition>
    </div>
</template>

<script>
// import 'vue2-dropzone/dist/vue2Dropzone.min.css'
 import axios  from 'axios'
 import VueWorker from 'vue-worker'

export default {
    name:'CarDetail',
    props: ['showModal_AddCar','Cardata'],
      components: {
      },
    data(){
        return {
          Path_AddData : "https://boxchen-highgoapi.herokuapp.com/api/v1/car/",
          Path_DeleteCloudinary : "https://boxchen-highgoapi.herokuapp.com/api/v1/cloudinary/",
          Path_CreateCloudinary :  "https://boxchen-highgoapi.herokuapp.com/api/v1/cloudinary/",
          CarDatailAdd: null,
          ImgUpload_cache:[],  //上傳圖片的暫存區
          ImgView_cache:[],      //檢視圖片的暫存區
          locationLat:null,
          ImgSelectViewA:-1,
          ImgSelectViewB:-1,
          isFixBtnhide: true,
          isSlide: false,
          isImgSelect_Arrow: false,
          Num : 0,
          ID : -1,
          type : '',
          plate: '',
          cc:'',
          capacity:'',
          fee_nor:'',
          fee_holi:'',
          location:'',
          type_change : '',
          plate_change: '',
          cc_change:'',
          capacity_change:'',
          fee_nor_change:'',
          fee_holi_change:'',
          location_change:'',
          ImgSelectView_A:-1,
          ImgSelectView_B:-1,
          BlockPos_XOff:0,
          Blocktransition:'200ms',
          inner_width:0,
          inner_XOff: 0,
          ImgBtnShow:0,
          ViewImgBtnSrc_delect:require('../../assets/delect.png'),
          ViewImgBtnSrc_Main:require('../../assets/isMainBG.png'),
          testinfo:null
        }

    },
    computed: {
      SelectImg_BlockPosLeft: function () {
        return this.BlockPos_XOff +"px";
      },
      transitionTime: function () {
        return this.Blocktransition;
      },
      Img_inner_width: function () {
        return this.inner_width+"px";
      },
      Img_inner_XOff: function () {
        return this.inner_XOff+"px";
      },
      ImgBtnsShow:function(){
        return this.ImgBtnShow ;
      }
    },
     methods: {
         Switch_CarDetail: function() {
                if(this.isFixBtnhide == true)
                      this.$parent.showModal_AddCar = false;
                else
                {
                    var leave_notsave = window.confirm("資料未儲存，確定離開?")
                    //離開刪除
                    if (leave_notsave) {
                        var  self = this;
                             self.ImgUpload_cache =[];
                             self.ImgView_cache   =[];
                             self.inner_XOff =0;
                             self.BlockPos_XOff =0; 
                         self.$parent.showModal_AddCar = false; 
                    }
                    else {
                    }
                }     
            },

            AddPanelInit:function() {
             
            var photos=[];
            this.CarDatailAdd = {
              "ID" : -1,
              "Type": "",
              "LicensePlate": "",
              "CC": null,
              "Capacity":null,
              "FeeNormal": null,
              "FeeHoliday":null,
              "Location": "",
              "photos": photos
            }


            this.Num       = -1 ;
            this.ID        = -1;  
            this.type      =this.CarDatailAdd.Type;
            this.plate     =this.CarDatailAdd.LicensePlate;
            this.cc        =this.CarDatailAdd.CC;
            this.capacity  =this.CarDatailAdd.Capacity;
            this.fee_nor   =this.CarDatailAdd.FeeNormal;
            this.fee_holi  =this.CarDatailAdd.FeeHoliday;
            this.location  =this.CarDatailAdd.Location;

            this.type_change      =this.CarDatailAdd.Type;
            this.plate_change     =this.CarDatailAdd.LicensePlate;
            this.cc_change        =this.CarDatailAdd.CC;
            this.capacity_change  =this.CarDatailAdd.Capacity;
            this.fee_nor_change   =this.CarDatailAdd.FeeNormal;
            this.fee_holi_change  =this.CarDatailAdd.FeeHoliday;
            this.location_change  =this.CarDatailAdd.Location;

            this.isFixBtnhide = true;
            
            this.ImgInnerWidth_Change();
            this.BlockPos_XOff =0;
            },
            CheckType:function(Ori,Change)
            {
                if(Ori != Change)
                {
                  this.isFixBtnhide = false;
                }

            },
            ClickCarPhotoSelect:function(index)
            {
              if(index ===this.ImgSelectView_A) return;
              if(this.isSlide == true) return;

               this.ImgSelectViewB = index ;
                //Inner Pos_X
                this.BlockPos_XOff = (110*index) +this.checkSelectPos(this.inner_XOff)*110;

              if(this.isSlide == false)
              {
                this.isSlide = true;

                setTimeout(() => {
                      this.ImgSelectViewA = this.ImgSelectViewB ;
                      setTimeout(() =>{
                        this.isSlide = false;
                        },120);
                      }, 450);
              }
            },
            Click_Arrow_Left:function()
            {
                if(this.checkSelectPos(this.inner_XOff) >=0) return;
                 this.inner_XOff = this.inner_XOff + 110 ;
                  this.BlockPos_XOff = this.BlockPos_XOff + 110 ;
            },
            Click_Arrow_Right:function()
            {
               if(this.checkSelectPos(this.inner_XOff) <= -(this.CarDatailAdd.photos.length-5))  return;
                 this.inner_XOff = this.inner_XOff - 110 ;
                 this.BlockPos_XOff = this.BlockPos_XOff - 110 ;
            },
            checkSelectPos: function (pos) {
              return  Math.round(pos /110);
            },
            UploadImg:function(event){
              const file = event.target.files[0];
              var CachePath = URL.createObjectURL(file);
                 var photo ={
                      "URL" : CachePath,
                      "Data": file,
                      "IsMain": 0,
                      "Add":    1
                 };
              this.ImgUpload_cache.push(photo);
              this.ImgView_cache.push(photo);
              this.ImgInnerWidth_Change() ;
              this.isFixBtnhide = false;
            },
            BtnSrc_deleteFun: function ()
            {
              for(var i=0;i < this.ImgView_cache.length;i++) 
              {
                if(deleteNum ==i)
                {
                  //lastImg
                  if(deleteNum >= this.ImgView_cache.length-1)
                  {
                    this.ImgSelectViewA = deleteNum-1;
                  }
                  else
                  {
                    this.ImgSelectViewA = deleteNum;
                  }

                  //Delete
                  if(this.ImgView_cache[i].hasOwnProperty('Add'))
                  {
                     for(var j=0;j<this.ImgUpload_cache.length;j++)
                      {
                        if(this.ImgView_cache[i].URL == this.ImgUpload_cache[j].URL)
                        {
                          this.$delete(this.ImgUpload_cache,j);
                          this.$delete(this.ImgView_cache,i);
                          break;
                        }
                      }
                  }
                  else
                  {
                    this.$delete(this.ImgView_cache,i);
                  }

                  this.ImgInnerWidth_Change();
                  if(this.ImgView_cache.length <=5)
                  {
                    this.inner_XOff =0;
                  }
                  else
                  {
                    this.inner_XOff =this.inner_XOff -110;
                  }
                  this.BlockPos_XOff = (110*this.ImgSelectViewA) +this.checkSelectPos(this.inner_XOff)*110;

                  this.isFixBtnhide = false;
                  return;
                }
              }
                console.log("BtnSrc_deleteFun");
            },
            BtnSrc_MainFun:function()
            {
                console.log("BtnSrc_MainFun");
            },
            ImgInnerWidth_Change:function(){
              this.inner_width =  110 * (this.ImgView_cache.length) ;
            },
           async  AddData() {
               var self = this;
               self.$parent.Active_LoadingView(true);
               self.isDataUplode = false;

               self.locationLat =null; 
              //Get GeoCode Data
              self.locationLat = await self.$parent.queryLocation(self.location_change,
              function(error){
                  console.log(error);
              });
              
              //Data Upload
               const fixdata = new FormData();
               fixdata.append('Type', this.type_change);
               fixdata.append('LicensePlate', this.plate_change);
               fixdata.append('CC', this.cc_change);
               fixdata.append('Capacity', this.capacity_change);
               fixdata.append('FeeNormal', this.fee_nor_change);
               fixdata.append('FeeHoliday', this.fee_holi_change);
               fixdata.append('Location', this.location_change);

               if(self.locationLat !=null)
                {
                  fixdata.append('CoordLat', self.locationLat.lat);
                  fixdata.append('CoordLong', self.locationLat.lng);
                }


                self.$parent.AxiosPost(self.Path_AddData,fixdata,
                function(succuess){
                          self.isDataUplode = true;
                          self.UpLoadCount = self.ImgUpload_cache.length ;
                          //Img Upload                
                          if(self.ImgUpload_cache!=null)
                          {
                            if(self.ImgUpload_cache.length >0)
                            {
                                self.ImgUpload_cache.forEach(
                                function(data) {
                                    var fd = new FormData();
                                    fd.append('id_car', succuess.data.data.ID);
                                    fd.append('photo',data.Data,data.Data.name);

                                    self.$parent.AxiosPost(self.Path_CreateCloudinary,fd,
                                    function(succuess2){
                                            self.UpLoadCount --;
                                            self.FinishUpload();
                                            
                                    },function(err){
                                          console.log("err :"+err);
                                            self.UpLoadCount --;
                                            self.FinishUpload();
                                    });
                                });
                            }
                            else
                            {
                              self.UpLoadCount =0;
                              self.FinishUpload();
                            }
                          }
                          else
                          {
                            self.FinishUpload();
                          }

                          
                },
                function(err){
                      console.log("err :"+err);
                      self.isDataUplode = true;
                      self.$parent.Active_LoadingView(false);
                });
            },
             FinishUpload:function() {
                  var self = this;
                  if(self.isDataUplode)
                  {
                    if( self.UpLoadCount==null || self.UpLoadCount <=0)
                    {
                      self.isDataUplode = false;
                      self.isDataDelete = false;
                      self.ImgUpload_cache =[] ;  
                      self.$parent.Active_LoadingView(false);
                      self.$parent.showModal_AddCar = false;
                      self.$parent.loadingData();  
                      self.inner_XOff =0;
                      self.BlockPos_XOff =0;
                          
                    }
                  }
            }
     }
}
</script>

<style >

.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}
.loadinggif{
    position: absolute;
  width: 125px;
  height: 125px;
  left: 46.5%;
  top:45%;
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}
.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 600px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-header {
  margin-top: 0;
  color: #42b983;
}
.modal-header{
  width:auto;
  height:30px;
}
.modal-body {
  padding: 0;
  margin: 0;

}
.modal-footer{
    width:auto;
  height: 30px;
}
.modal-close-button {
  float: right;
  margin-right: 1px;
  background-color: rgb(250, 92, 92);
  color:white;
  border:0px;
  width: 25px;
  height: 25px;
       -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
     -moz-user-select: none; /* Firefox */
     -ms-user-select: none; /* Internet Explorer/Edge */
     user-select: none; 
}
.modal-close-button:active {
  background-color: rgb(110, 40, 40);
}
.modal-default-button {
  float: right;
  margin-right: 1px;
  background-color: rgb(231, 225, 225);
  border:0px;
  width: 70px;
  Height:30px;
}
/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}
.modal-container{
 padding: 1%;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
.css_CarTitle {
    height: 36px;
    padding: 4px 10px 2px 10px;
    color: #253363;
    background-color: rgb(218, 218, 218);
    text-align: center;
         -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
     -moz-user-select: none; /* Firefox */
     -ms-user-select: none; /* Internet Explorer/Edge */
     user-select: none; 
}
.css_CarContent {
    padding: 0px 10px 8px 10px;
    text-align: center;
    border-top: 1px solid rgb(218, 218, 218);
    border-right: 1px solid rgb(218, 218, 218);
    width: 138px;

}
.css_CarContent input{
    text-align: center;
    padding: 0%;
    margin-bottom:-5px; 
    width: 90%;
}
.btn_show{
  display: none;
}
.body_Img{
  width: auto;
  height: 440px;
  overflow: hidden;
           -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
     -moz-user-select: none; /* Firefox */
     -ms-user-select: none; /* Internet Explorer/Edge */
     user-select: none; 
}

.body_ImgView_Slider_Pos{
  position: relative;
  width: auto;
  height: 310px;
  
}
.body_ImgView_Slider{
  transform:  translateX(-592px) ;
  transition: 500ms;
}
.body_ImgView_Frame{
  position: relative;
  background-color: rgb(252, 252, 252);
  width: 97%;
  height: 97%;
  position: absolute;
  overflow: auto;
  margin: auto;
  top: 0; left: 0; bottom: 1; right: 0;
  box-shadow: 2px 2px 4px 1px rgba(0, 0, 0, 0.3);
}

.body_ImgView_Frame_2{
  background-color: rgb(252, 252, 252);
  width: 97%;
  height: 97%;
  position: absolute;
  overflow: auto;
  margin: auto;
  top: 0; left: 600px; bottom: 1; right: 0;
  box-shadow: 2px 2px 4px 1px rgba(0, 0, 0, 0.3);
}
.body_ImgSelect{
  background-color: rgb(231, 231, 231);
  width: auto;
  height: 90px;
  position: relative;
}
.body_ImgSelect_Now{
  position: absolute;
  background-color: rgb(165, 165, 165);
  top: 5px; bottom: 1; right: 0;
  width: 110px;
  height: 80px;
   z-index:100;
            -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
     -moz-user-select: none; /* Firefox */
     -ms-user-select: none; /* Internet Explorer/Edge */
     user-select: none; 
}
.body_ImgSelect_inner {
  position: absolute;
   top: 0; left: 0px; bottom: 0; right: 0;
  margin: 10px 0 0 0;
  z-index: 101;
           -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
     -moz-user-select: none; /* Firefox */
     -ms-user-select: none; /* Internet Explorer/Edge */
     user-select: none; 
}
.body_ImgSelect_innerImg {
  display: inline-block;
  margin: 5px 0 0 1px 10px;
  z-index: 101;
           -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
     -moz-user-select: none; /* Firefox */
     -ms-user-select: none; /* Internet Explorer/Edge */
     user-select: none; 

}
.body_ImgSelect_Img{
  width: 100px;
  height: 66.66px;
  margin: 0 5px 0 5px;
  cursor: pointer;
  z-index: 102;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; 
}
.body_ImgSelect_Arrow{
   position: absolute;
   width: 25px;
   background-color: rgba(0, 0, 0, 0.5);
   z-index: 102;
   cursor: pointer;
}
.body_ImgSelect_Arrow:active{
   background-color: rgba(90, 90, 90, 0.7);

}
.ArrowImg{
     width: 90%;
     height: auto;
     margin-top : 35px;
     display:  block;
     -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
     -moz-user-select: none; /* Firefox */
     -ms-user-select: none; /* Internet Explorer/Edge */
     user-select: none; 

}
.ArrowImf_Filp{
  -webkit-transform: scaleX(-1);
   transform: scaleX(-1);
}
.ArrayLeft{
     top: 0; left: 0px; bottom: 0; right: 0;
}
.ArrayRight{
     top: 0;  bottom: 0; right: 0;
}
.body_View_Img{
  width: 95%;
  height: 95%;
  margin: 1.5%  0 0 2.5%;
}
.View_Img_Btn{
  position: absolute;
  width:45px;
  height: 45px;
  cursor: pointer;
}
.View_Img_Btn:active{
  opacity: 0.3;
}
.delectpos{
  top: 80%;
  left: 87%;
}
.Mainpos{
  top: 80%;
  left: 77%;
}
.fileBtnDiv{
  position: relative;
  height: 40px;
      -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none;
}

input[type="file"]{
  display: none;
}
label{
  color:white;
  height: 30px;
  width:580px;
  background-color: #3e7d96;
  position: absolute; 
  margin: auto;
  top:0;
  bottom: 0;
  Left:0;
  right:0;
  font-size: 20px;
  display:flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
label:active{
    background-color: #214250;
}

</style>


