<template>
    <div class="container" v-bind:class="{'container-close':!menu_selected}">
        <div class="Side">
            <div class="addCell"  v-on:click="ClickDataAdd()">
                <div class="addCellword Unselect">
                    +
                </div>
            </div>
              <div class="col s4 m6">
                  <!-- CarData -->
                   <div v-if="Stat_Now ==0"    v-for="(getCar,index) in getleftCars" :key="index">
                        <div  class="cardwidth" >
                                    <div class="card-image" style="float: left;">
                                        <img   :src="GetCarMainImg(index)">
                                    </div>
                                    <div class="card-stacked" style="float: right;">  
                                        <div style="margin-top:-20px; padding: 0px;">
                                                <div class="Carstyle Unselect">
                                                    <p>{{getCar.Type}}</p>
                                                </div>
                                                <div class="CarMark Unselect">
                                                    <p>{{getCar.LicensePlate}}</p>
                                                </div>
                                        </div>

                                        <div style="display:  block; margin-top:-35px;padding: 0px; height: 45px;">
                                            <img class="card-stacked-btn Unselect btnpointer cardClick" src="../../assets/trashcan.png" v-on:click="ClickCarDelete(index)">
                                            <img class="card-stacked-btn Unselect btnpointer cardClick" src="../../assets/detail.png"   v-on:click="ClickCarSelect(index)">
                                            <img class="card-stacked-btn Unselect btnpointer cardClick" src="../../assets/location.png" v-on:click="ClickCarlocation(index)">
                                        </div>                         
                                    </div>
                        </div>
                    </div> 
                    <!-- CustomData -->
                   <div v-if="Stat_Now ==1"    v-for="(getCustomer,index) in getleftCustomers" :key="index">
                        <div  class="cardwidth" >
                                    <div class="card-stacked" style="float: Left;">  
                                        <div style="margin-top:-20px; margin-Left:20px; padding: 0px;">
                                                <div class="Carstyle Unselect" style="display:  block; ">
                                                    <p>暱稱:{{getCustomer.NickName}}</p>
                                                </div>
                                                <div class="CusMark Unselect" style="display:  block; margin-top:-20px;">
                                                    <p>電話:{{getCustomer.Phone}}</p>
                                                </div>
                                        </div>

                                        <div style="display:  block; margin-top:-47px; margin-right:-112px;padding: 0px; height: 45px; float: Right;">
                                            <img class="card-stacked-btn Unselect btnpointer cardClick" src="../../assets/trashcan.png" v-on:click="ClickCusDelete(index)">
                                            <img class="card-stacked-btn Unselect btnpointer cardClick" src="../../assets/detail.png"   v-on:click="ClickCusSelect(index)">
                                            <!-- <img class="card-stacked-btn Unselect btnpointer cardClick" src="../../assets/location.png" > -->
                                        </div>                         
                                    </div>
                        </div>
                    </div> 
              </div>
            

        </div>
        <div class="Btns Unselect">
            <div class="btn btn_menu btn_active Unselect" @click="click_menu" ><img class="btn_img_menu " src="../../assets/menu.png"></div>
            <div class="btn btn_property Unselect" v-bind:class="{'btn_property-Hide':!Menu_selected ,  'btn_property-Show':Menu_selected ,'btn_active': this.Stat_Now ==0}" v-on:click="click_ChooseStatus(0)"><img class="btn_img "  v-bind:class="{'btn_img_unselect':this.Stat_Now !=0} "   src="../../assets/car.png"></div>
            <div class="btn btn_property Unselect" v-bind:class="{'btn_property-Hide':!Menu_selected,  'btn_property-Show':Menu_selected ,'btn_active': this.Stat_Now ==1}"  v-on:click="click_ChooseStatus(1)"><img class="btn_img"   v-bind:class="{'btn_img_unselect':this.Stat_Now !=1} "   src="../../assets/man.png"></div>
        </div>
    </div>

</template>

<script>
 import axios  from 'axios'
 import * as Enum  from '@/components/Enum/Enum.js'
export default {
    name:'LeftBar',
    props: ['getleftCars','getleftCustomers'],
    data(){
        return {
            Path_DeleteCarData: "https://boxchen-highgoapi.herokuapp.com/api/v1/car/",
            Path_DeleteCusData: "https://boxchen-highgoapi.herokuapp.com/api/v1/coustomer/",
            menu_selected: true,
            pro: false, 
            Stat_Now : Enum.EnumDataStatus.Car,
            CarMainImg:[]
        }
    },
    computed: {
        Menu_selected: function () {
             return this.menu_selected;
      }
    },
    methods: {
        click_menu: function() {
                this.menu_selected = !this.menu_selected
            },
        click_ChooseStatus: function(Num)
            {
               if(Num ==0)
                 this.Stat_Now = Enum.EnumDataStatus.Car;
                else  if(Num ==1)
                 this.Stat_Now = Enum.EnumDataStatus.Customer;
            },
        StatusDisplay: function (status) {
               if( this.Stat_Now ==status )  
               {
                   return true;
               }
                else  
               {      
                   return false;  
               }           
            },
        ClickCarSelect: function(data){
                setTimeout(()=>{this.$parent.SelectCar(data);},150)
             },
        ClickCusSelect: function(data){
                setTimeout(()=>{this.$parent.SelectCustom(data);},150)
             },
        ClickCarlocation: function(data){
            this.$parent.SetMapCarCenter(data);
        },
        ClickDataAdd: function(){
                if(this.Stat_Now == Enum.EnumDataStatus.Car)
                    setTimeout(()=>{this.$parent.SelectCarAdd();},150);
                if(this.Stat_Now == Enum.EnumDataStatus.Customer)
                    setTimeout(()=>{this.$parent.SelectCustomerAdd();},150)
             },
        ClickCarDelete: function(data){
            var self =this;
                    self.$parent.Active_LoadingView(true);
                    axios.delete(self.Path_DeleteCarData+self.getleftCars[data]["ID"])
                    .then(res =>{
                         self.$parent.DeleteOneCarData(data);
                         self.$parent.Active_LoadingView(false);
                    })
                    .catch(function (error) {
                        console.log("==>"+error);
                        self.$parent.Active_LoadingView(false);
                    }
                    )


              },
            ClickCusDelete: function(data){
                    this.$parent.Active_LoadingView(true);
                    axios.delete(this.Path_DeleteCusData+this.getleftCustomers[data]["ID"])
                    .then(res =>{
                         this.$parent.DeleteOneCusData(data);
                         this.$parent.Active_LoadingView(false);
                    })
                    .catch(function (error) {
                        console.log("==>"+error);
                        this.$parent.Active_LoadingView(false);
                    }
                    )


              },
        GetCarMainImg:function(index){
            for(var i=0;i<this.getleftCars.length;i++)
            {
               if(index ==i)
               {
                   if(this.getleftCars[i].photos ==null) return null;

                   for(var j=0;j<this.getleftCars[i].photos.length;j++)
                   {
                       if(this.getleftCars[i].photos[j].isMain==1)
                       {
                           return  this.getleftCars[i].photos[j].URL;
                       }
                   }
               }
            }
        }

    },  created() {
    }

}
</script>

<style Lang="scss" scoped>
.addCell{
    position: relative;
    text-align:center;
     margin-top:3px;
     margin: auto;
     background-color: white;
     height: 40px;
     width: 305px;
}
.addCellword{
     position: absolute;
     top:50%;
     left: 50%;
     transform: translate(-50%, -52%);
     font-size: 40px;
}
.addCell:active{
     background-color: rgb(184, 184, 184);
}
.container{
    position: absolute;
    float: left;
    top:10;
    left:0;
    width:350px;
    padding: 0;
    min-height: 100%;
    background-color: rgba(202, 26, 26, 0);
    z-index: 2;
    transition: all .5s ease-in-out;
}

.container-close{
    transform: translate(-90%,0);
}

.Side{
    position: absolute;
    float: left;
    width:310px;
    min-height: 100%;
    background-color: rgba(230, 230, 230, 0.95);
    z-index: 5;
    border-style:solid;
    border-width: 0 1.2px 0 0;
    border-color: rgba(155, 155, 155, 0.5);
    box-shadow: 0 0 0.5em rgba(63, 63, 63,0.5);
}
.Btns{
    position: absolute;
    left: 310px;
    width:40px;
    min-height: 100%;
    background-color: rgba(240, 240, 240, 0.0);
}

.btn_property{
    margin-bottom:3px;
    width:40px;
    height: 80px;
    margin-left: -2px;
    padding: 0px;
    background-color: rgba(141, 132, 132,0.5);

}
.btn_property-Hide{
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.1s, opacity 0.1s linear;
}
.btn_property-Show{
    visibility: visible;
    opacity: 1;
    transition: visibility 0.1s, opacity 0.1s linear;
}
.btn_active{
        transition: all .1s ease-in-out;
     background-color: rgb(248, 248, 248);
}
.btn_menu{
    margin-bottom:2px;
    width:40px;
    height: 40px;
    margin-left: -2px;
    padding: 0px;
    
}
.btn_img{
    margin-top: 20px;
    width:35px;
    height: auto;
}
.btn_img_unselect{
    opacity:0.3;
}
.btn_img_menu{
    margin-top: 2px;
    margin-left: 2px;
    width:35px;
    height: auto;
}
.cardwidth{
    padding: 0%;
    margin: 2px 2px 2px 4px;
    height: 90px;
    background-color: rgb(255, 255, 255);
}
.card-image{
   width: 110px;
   Height: 90px;
   padding: 0;
   float: left;
}
.card-image img{
   width: 100px;
   Height: 80px;
   padding: 0;
   margin-left: 5px;
   margin-top: 5px;
}
.card-stacked{
    display:  block;
    width: 190.81px;
   height: 90px;
}
.Carstyle{
    text-align: left;
    font-size: 22px;
    font-weight:bold;
    color: rgb(10, 49, 156);
}
.CarMark{
    text-align: left;
    font-size: 23px;
    margin-top: -35px;
}
.CusMark{
    text-align: left;
    font-size: 22px;
    margin-top: -35px;
}
.card-stacked-btn{
   padding: 0px;
   width: 25px;
   Height: 30px;
   float: right;
   margin-top: 15px;
   margin-left: 3px;
}

.Unselect{
-webkit-touch-callout: none; /* iOS Safari */
-webkit-user-select: none; /* Safari */
-khtml-user-select: none; /* Konqueror HTML */
-moz-user-select: none; /* Firefox */
-ms-user-select: none; /* Internet Explorer/Edge */
user-select: none; 
}


.btnpointer{
    cursor: pointer;
}


.cardClick:active{
background-color: rgb(184, 184, 184);
}
</style>



