<template>
            <div v-if="isLoading">
                <div class="modal-mask" >
                  <img class="loadinggif"  src="../../assets/loading.gif" >
               </div>
            </div>
</template>

<script>
export default {
        name:'Loading',
        props: ['isLoading'],
        data(){
        return {
        }
        },
          methods: {

         }
}
</script>

<style >

.modal-mask {
  position: fixed;
  z-index: 10000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}
.loadinggif{
    position: absolute;
  width: 125px;
  height: 125px;
  left: 46.5%;
  top:45%;
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}
</style>


