export const  user = {
    userNotLogin :0,
    userLogin :1
  }
  function userchange(userNum){
    if(userNum==0)
    {
      userNow = user.userNotLogin;
    }
    else  if(userNum==1)
    {
      userNow = user.userLogin;
    }
 }
export  var  userNow = user.userNotLogin;
export  { userchange };


