<template>
    <div  v-if="showModal_AddCustomer" @close="showModal_AddCustomer = false">
            <transition name="modal">
                <div class="modal-mask">
                <div class="modal-wrapper">
                    <div class="modal-container">

                    <div class="modal-header">
                        <button class="modal-close-button" @click="Switch_CustomDetail()">
                            X
                        </button>
                    </div>

                    <div class="modal-body">
                      <!-- Data -->
                          <table  border="0" cellpadding="0" cellspacing="0" style="padding:0;margin-top:-10px;">
                              <tbody>
                                    <td align="center" valign="top" style="padding-left:0;padding-right:0;">
                                      <table width="100%" border="1" cellpadding="1" cellspacing="1">
                                          <tbody>
                                              <tr>
                                                  <td class="css_CarTitle">名字</td>
                                                  <td class="css_CarContent"><input v-model="Data_Modify[1]" value="Name"  v-on:input="CheckType(Data_Ori[1],Data_Modify[1])"></td>
                                                  <td class="css_CarTitle">暱稱</td>
                                                  <td  class="css_CarContent"><input v-model="Data_Modify[2]" value="NickName" v-on:input="CheckType(Data_Ori[2],Data_Modify[2])"></td>
                                             </tr>
                                              <tr>
                                                  <td class="css_CarTitle">身分證</td>
                                                  <td class="css_CarContent" colspan="3"><input  v-model="Data_Modify[3]" value="IdCard" v-on:input="CheckType(Data_Ori[3],Data_Modify[3])"></td>  
                                              </tr>
                                              <tr>                                          
                                                  <td class="css_CarTitle">生日</td>
                                                  <td class="css_CarContent" colspan="3"><vue-datepicker-local  class="css_DateRect" v-model="time" v-on:input="CheckData(StringToDate(Data_Ori[4]))" /></td>
                                              </tr>
                                              <tr> 
                                                  <td class="css_CarTitle">教育程度</td>
                                                  <td class="css_CarContent" colspan="3"><input v-model="Data_Modify[5]" value="Education"  v-on:input="CheckType(Data_Ori[5],Data_Modify[5])"></td>
                                              </tr>
                                              <tr>
                                                  <td class="css_CarTitle">地址</td>
                                                  <td class="css_CarContent" colspan="3"><input v-model="Data_Modify[6]" value="Address" v-on:input="CheckType(Data_Ori[6],Data_Modify[6])"></td>
                                              </tr>
                                              <tr>                                                                                              <tr>
                                                  <td class="css_CarTitle">電子信箱</td>
                                                  <td class="css_CarContent" colspan="3"><input v-model="Data_Modify[7]" value="Email" v-on:input="CheckType(Data_Ori[7],Data_Modify[7])"> </td>
                                              </tr>
                                               <tr>                                                                                              <tr>
                                                  <td class="css_CarTitle">電話</td>
                                                  <td class="css_CarContent" colspan="3"><input v-model="Data_Modify[8]" value="Phone" v-on:input="CheckType(Data_Ori[8],Data_Modify[8])"> </td>
                                              </tr>
                                          </tbody>
                                      </table>
                                    </td>

                              </tbody>
                          </table>
                    </div>


                    <div class="modal-footer">
                        <button class="modal-default-button"  v-bind:class="{'btn_show':isFixBtnhide}"  @click="AddData()">
                            新增
                        </button>
                    {{createerrorinfo}}
                    </div>


                    </div>
                </div>
                </div>
            </transition>
    </div>
</template>

<script>
// import 'vue2-dropzone/dist/vue2Dropzone.min.css'
 import axios  from 'axios'
 import VueWorker from 'vue-worker'
 import VueDatepickerLocal from 'vue-datepicker-local';

// var state = {
//   date: new Date(2016, 9,  16)
// }
export default {
    name:'CarDetail',
    props: ['showModal_AddCustomer','Cardata'],
      components: {
          VueDatepickerLocal
      },
    data(){
        return {
          Path_AddData : "https://boxchen-highgoapi.herokuapp.com/api/v1/coustomer/",
          CustomerDatailAdd: null,
          isFixBtnhide: true,
          time:new Date(),
          Num : 0,
          ID : -1,
          Data_Ori:[],
          Data_Modify:[],
         createerrorinfo:null
        }

    },
    computed: {
    },
     methods: {
         Switch_CustomDetail: function() {
                var self =this;
                if(self.isFixBtnhide == true)
                      self.$parent.showModal_AddCustomer = false;
                else
                {
                    var leave_notsave = window.confirm("資料未儲存，確定離開?")
                    //離開刪除
                    if (leave_notsave) {
                        self.$parent.Active_LoadingView(false);
                        self.$parent.showModal_AddCustomer = false;
                    }   
                    else{
                        self.$parent.Active_LoadingView(false);;
                    }

                    }
            },
            AddPanelInit:function() {
             
            this.CustomerDatailAdd = {
              "ID"       : -1,
              "Name"     : "",
              "NickName" : "",
              "IdCard"   : "",
              "Birth"    : null,
              "Education": "",
              "Address"  :"",
              "Email"    : "",
              "Phone"    : ""
            }
            this.Num       = -1 ;
            this.ID        = -1; 
            this.Data_Ori     =[];
            this.Data_Modify  =[];
            this.Data_Ori.push(
                this.CustomerDatailAdd.ID,
                this.CustomerDatailAdd.Name,
                this.CustomerDatailAdd.NickName,
                this.CustomerDatailAdd.IdCard,
                this.CustomerDatailAdd.Birth,
                this.CustomerDatailAdd.Education,
                this.CustomerDatailAdd.Address,
                this.CustomerDatailAdd.Email,
                this.CustomerDatailAdd.Phone
            ); 

            this.Data_Modify.push(
                this.CustomerDatailAdd.ID,
                this.CustomerDatailAdd.Name,
                this.CustomerDatailAdd.NickName,
                this.CustomerDatailAdd.IdCard,
                this.CustomerDatailAdd.Birth,
                this.CustomerDatailAdd.Education,
                this.CustomerDatailAdd.Address,
                this.CustomerDatailAdd.Email,
                this.CustomerDatailAdd.Phone
            ); 

            this.isFixBtnhide = true;
            },
            CheckType:function(Ori,Change)
            {
                if(Ori != Change)
                {
                  this.isFixBtnhide = false;
                }
            },
            CheckData:function(Ori,Change)
            {
              Change = this.formatDate(this.time);

              this.Data_Modify[4] = Change ;

                if(Ori != Change)
                {
                  this.isFixBtnhide = false;
                }
               // console.log("data :"+Change);
            },
            AddData:function() {
                var self = this;

                self.$parent.Active_LoadingView(true);
                const fixdata = new FormData();
                fixdata.append('Name',      self.Data_Modify[1]);
                fixdata.append('NickName',  self.Data_Modify[2]);
                fixdata.append('IdCard',    self.Data_Modify[3]);
                fixdata.append('Birth',     self.Data_Modify[4]);
                fixdata.append('Education', self.Data_Modify[5]);
                fixdata.append('Address',   self.Data_Modify[6]);
                fixdata.append('Email',     self.Data_Modify[7]);
                fixdata.append('Phone',     self.Data_Modify[8]);

                axios.post(self.Path_AddData,fixdata)
                .then(res =>{
                  this.createerrorinfo = res.data;
                  if(res.data.status ==200)
                  {
                    self.$parent.SelectCustomerAddCallBack(res.data.data);
                    self.$parent.showModal_AddCustomer = false;  
                  }
                  self.$parent.Active_LoadingView(false);
                })
                .catch(function (error) {
                  console.log("==>"+error);
                   self.$parent.Active_LoadingView(false);
                }
                )
            },
              formatDate:function(date) {
                var monthNames = [
                  "01", "02", "03",
                  "04", "05", "06", "07",
                  "08", "09", "10",
                  "11", "12"
                ];

                var day = date.getDate();
                var monthIndex = date.getMonth();
                var year = date.getFullYear();

                return year + '-' + monthNames[monthIndex] + '-' + day;
              },
              StringToDate:function(Txt) {

                  if(Txt ==null)
                  {
                     var d =  new Date();
                     return d;
                  }

                  var datearray = Txt.split('/');
                 return mydate = new Date(datearray[0]+'-'+datearray[1]+'-'+datearray[2]);
              }
              
     }
}
</script>

<style >

.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}
.loadinggif{
    position: absolute;
  width: 125px;
  height: 125px;
  left: 46.5%;
  top:45%;
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}
.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 600px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-header {
  margin-top: 0;
  color: #42b983;
}
.modal-header{
  width:auto;
  height:30px;
}
.modal-body {
  padding: 0;
  margin: 0;

}
.modal-footer{
    width:auto;
  height: 30px;
}
.modal-close-button {
  float: right;
  margin-right: 1px;
  background-color: rgb(250, 92, 92);
  color:white;
  border:0px;
  width: 25px;
  height: 25px;
       -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
     -moz-user-select: none; /* Firefox */
     -ms-user-select: none; /* Internet Explorer/Edge */
     user-select: none; 
}
.modal-close-button:active {
  background-color: rgb(110, 40, 40);
}
.modal-default-button {
  float: right;
  margin-right: 1px;
  background-color: rgb(231, 225, 225);
  border:0px;
  width: 70px;
  Height:30px;
}
/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}
.modal-container{
 padding: 1%;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
.css_CarTitle {
    height: 36px;
    padding: 4px 10px 2px 10px;
    color: #253363;
    background-color: rgb(218, 218, 218);
    text-align: center;
         -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
     -moz-user-select: none; /* Firefox */
     -ms-user-select: none; /* Internet Explorer/Edge */
     user-select: none; 
}
.css_CarContent {
    padding: 0px 10px 8px 10px;
    text-align: center;
    border-top: 1px solid rgb(218, 218, 218);
    border-right: 1px solid rgb(218, 218, 218);
    width: 138px;

}
.css_CarContent input{
    text-align: center;
    padding: 0%;
    margin-bottom:-5px; 
    width: 90%;
}
.btn_show{
  display: none;
}
.body_Img{
  width: auto;
  height: 440px;
  overflow: hidden;
           -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
     -moz-user-select: none; /* Firefox */
     -ms-user-select: none; /* Internet Explorer/Edge */
     user-select: none; 
}
.delectpos{
  top: 80%;
  left: 87%;
}
.Mainpos{
  top: 80%;
  left: 77%;
}
.fileBtnDiv{
  position: relative;
  height: 40px;
      -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none;
}

input[type="file"]{
  display: none;
}
label{
  color:white;
  height: 30px;
  width:580px;
  background-color: #3e7d96;
  position: absolute; 
  margin: auto;
  top:0;
  bottom: 0;
  Left:0;
  right:0;
  font-size: 20px;
  display:flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
label:active{
    background-color: #214250;
}
.css_DateRect{
  width: 56.5%;
  cursor: pointer;
}
</style>


