<template>
  <div id="app">
      <Navbar v-show="$route.path==='/login'? false:true"/>
    <router-view/>
  </div>
</template>

<script>
import Navbar from '@/components/layout/Navbar'
import GMap from '@/components/GMap/GMap'
import dropzone from '@/components/dropzone'
import babel from'babel-polyfill'
export default {
  name: 'App',
    components:{
    Navbar,
    GMap,
    dropzone,
    babel
  }
}
</script>

<style>
 @import './assets/vuedropzone.css';
body{
  background: #ddd;
   overflow: hidden;
}
</style>

<style lang="css">
  @import './assets/vuedropzone.css';
</style>


