<template>
    <div>  
         <CarDetail :showModal=showModal ref="CarDetailRef"  />
         <CarDetailAdd :showModal_AddCar=showModal_AddCar ref="CarDetailAddRef"  />
         <CustomerDetail  :showModal_customer=showModal_customer ref="CustomerDetailRef" />
         <CustomerDetailAdd  :showModal_AddCustomer=showModal_AddCustomer ref="CustomerDetailAddRef" />
         <LeftBar :getleftCars=getCars :getleftCustomers=getCumters ref="LeftBarRef" />
         <!-- <img src="../../assets/view.jpg" alt="Pink flamingo."> -->
         <div class="map">
             <div class="google-map" id="map"></div>
         </div>
         <Loading   :isLoading=showLoading ref="LoadingRef"/>
    </div>
</template>

<script>
 import LeftBar from '@/components/GMap/LeftBar'
 import CarDetail from '@/components/GMap/CarDetail'
 import CarDetailAdd from '@/components/GMap/CarDetail_Add'
 import CustomerDetail    from '@/components/GMap/CustomerDetail'
 import CustomerDetailAdd from '@/components/GMap/CustomerDetail_Add'
 import Loading from '@/components/GMap/Loading'
 import axios  from 'axios'

const $qs=null;

export default {
    $qs,
    name:'GMap',
    components:{
     LeftBar,
     CarDetail,
     CarDetailAdd,
     CustomerDetail,
     CustomerDetailAdd,
     Loading
  },
  data(){
      return {
          Path_GetCarData: "https://boxchen-highgoapi.herokuapp.com/api/v1/car/",
          Path_GetCoustomerData: "https://boxchen-highgoapi.herokuapp.com/api/v1/coustomer/",
          map:null,
          mapmarker:[],
          showModal: false,
          showModal_customer:false,
          showModal_AddCar :false,
          showModal_AddCustomer :false,
          showLoading:false,
          loadingFinish_Car :false,
          loadingFinish_Cus :false,
          getCars:[],
          getCumters:[],
      }
  },
  methods:{
        SelectCar: function(Num) {
               this.$refs['CarDetailRef'].CarDetail(this.getCars[Num],Num);  
               this.showModal = true;
            },
        SelectCustom: function(Num) {
               this.$refs['CustomerDetailRef'].CusDetail(this.getCumters[Num],Num);  
               this.showModal_customer = true;
            },
        SelectCarAdd: function() {
               this.$refs['CarDetailAddRef'].AddPanelInit();
               this.showModal_AddCar = true;
            },
        SelectCarAddCallBack: function(data) {
              this.getCars.push(data);
            },
        SelectCustomerAdd: function() {
               this.$refs['CustomerDetailAddRef'].AddPanelInit();
               this.showModal_AddCustomer = true;
            },
        SelectCustomerAddCallBack: function(data) {
              this.getCumters.push(data);
            },
        fixCarData:function(Num,type,plate,cc,capacity,fee_nor,fee_holi,location,Lat,Lng){
                this.getCars[Num].Type  = type;
                this.getCars[Num].LicensePlate = plate;
                this.getCars[Num].CC        = cc;
                this.getCars[Num].Capacity  = capacity;
                this.getCars[Num].FeeNormal   = fee_nor;
                this.getCars[Num].FeeHoliday  = fee_holi;
                this.getCars[Num].Location  = location;
                this.getCumters[Num].CoordLat      = Lat;
                this.getCumters[Num].CoordLong     = Lng;
            },
        fixCusData:function(Num,Name,NickName,IdCard,Birth,Education,Address,Email,Phone){
                this.getCumters[Num].Name          = Name;
                this.getCumters[Num].NickName      = NickName;
                this.getCumters[Num].IdCard        = IdCard;
                this.getCumters[Num].Birth         = Birth;
                this.getCumters[Num].Education     = Education;
                this.getCumters[Num].Address       = Address;
                this.getCumters[Num].Email         = Email;
                this.getCumters[Num].Phone         = Phone;
            },
            Active_LoadingView: function(Active) {
                   this.showLoading = Active;
            },
            DeleteOneCarData: function(Num) {
                //console.log(((this.getCars.length-1) - Num));
                 this.mapmarker[Num].setMap(null);
                 this.$delete(this.getCars,Num);
                 this.$delete(this.mapmarker,Num);

            },
            DeleteOneCusData: function(Num) {
                this.$delete(this.getCumters,Num);
            },
            loadingData()
            {
                    var self = this;
                    self.Active_LoadingView(true);
                    
                    //self.Get("test",self.Path_GetCarData);
                    //Muti Loading-Car
                    self.Init_loading(self.Path_GetCarData,(res)=>{
                        self.getCars = res.data ;
                            setTimeout(() =>
                                {
                                    self.loadingFinish_Car = true;
                                    self.RenderMap_Check();
                                    self.$refs['LeftBarRef'].GetCarMainImg(); 
                                },100); 
                    });
                    // //Muti Loading-Cus
                    // self.Init_loading(self.Path_GetCoustomerData,(res)=>{
                    //     self.getCumters = res.data ;
                    //         setTimeout(() =>
                    //             {
                    //                 self.loadingFinish_Cus = true;
                    //                 self.RenderMap_Check();
                    //             },100); 
                    // });

            },
            SetMapCarCenter: function(Num) {
                this.map.setCenter(new google.maps.LatLng(this.getCars[Num].CoordLat, this.getCars[Num].CoordLong));
            },
            async Init_loading(Path,event_success) {
                try {
                        var self = this;
                        // let res = await axios.get(Path,{
                        //     crossDomain:true, 
                        //     headers:{
                        //         "Access-Control-Allow-Origin": "*",
                        //         'Content-Type': 'application/json; charset=UTF-8'
                        //         }});

                        let res = await axios.get(Path,{
                            headers:{
                                "Access-Control-Allow-Origin": "*",
                                'Content-Type': 'application/json; charset=UTF-8;',
                                
                            }
                        });

                        event_success(res);
                }  catch (err) 
                {
                    setTimeout(() =>
                    {
                        console.log("again");
                     //   self.Init_loading(Path);
                    },5000); 
                }
            },
            async AxiosGet(Path,event_success,event_error) {
                try {
                        var self = this;
                        let res = await axios.get(Path, {'Content-Type': 'application/json; charset=UTF-8'});
                        event_success(res);
                }  catch (err) 
                {
                     event_error(err);
                }
            },
            async AxiosPost(Path,fd,event_success,event_error) {
                try {
                        var self = this;
                        let res = await axios.post(Path,fd);
                        event_success(res);
                }  catch (err) 
                {
                     event_error(err);
                }
            },
            async AxiosPut(Path,fd,event_success,event_error) {
                try {
                        var self = this;
                        let res = await axios.put(Path,fd);
                        event_success(res);
                }  catch (err) 
                {
                     event_error(err);
                }
            },
            async AxiosDelete(Path,event_success,event_error) {
                try {
                        var self = this;
                        let res = await axios.delete(Path);
                        event_success(res);
                }  catch (err) 
                {
                     event_error(err);
                }
            },
            async RenderMap_Check(){
                if(this.loadingFinish_Car && this.loadingFinish_Cus)
                {
                        var self =this;
                        await self.renderMap();
                        self.loadingFinish_Car = false;
                        self.loadingFinish_Cus = false;
                        self.Active_LoadingView(false);
                        self.mapmarker =[];
                        for(var i=0;i<self.getCars.length;i++){
                            self.addMapMarker(self.getCars[i].Type,self.getCars[i].CoordLat,self.getCars[i].CoordLong);
                        };
                }
            },
            renderMap(){
                    this.map = new google.maps.Map(document.getElementById('map'),{
                        center:{lat:24.166521,lng:120.601966},
                        zoom:18,
                        maxZoom:20,
                        minZoom:3,
                        streetViewControl:false
                    })
            },
            addMapMarker(labelstr,latnum,lngnum){
                    var marker = new google.maps.Marker({
                        position: {
                            lat:latnum,
                            lng:lngnum
                        },
                        icon: {
                        url: "http://maps.google.com/mapfiles/ms/icons/red-dot.png",
                        labelOrigin: new google.maps.Point(20, 40),
                        size: new google.maps.Size(32,32),
                        anchor: new google.maps.Point(16,32)
                        },
                        map:this.map,
                        label:{
                        text: labelstr,
                        fontSize: "18px",
                        color: "#C70E20",
                        fontWeight: "bold",
                        y: '200'
                        }
                    });

                    this.mapmarker.push(marker);
            },
            async queryLocation(address,error){
                    
                    var localtionOb = null;

                    var url = 'https://maps.googleapis.com/maps/api/geocode/json?address='+address+'&language=zh-TW&key=AIzaSyAvsoDqkvGa9QT2ffaGxG49DGAJEPk6bkA';
                  //  console.log(url);
                    //return;
                  await  this.AxiosGet(url,function(success){
                        localtionOb = success.data.results[0].geometry.location;
                    },
                    function(error){
                        console.log(error);
                    }
                    )
            return localtionOb;
            },
             Get(eventName, ApiPath) 
             {
                    console.log("【APITool】::Get=>「" + ApiPath + "」");
                    var me = this;
                    $.ajax({
                        type: 'GET',
                        url: ApiPath,
                        dataType: 'json',
                        crossDomain: true,
                        contentType: 'application/json; charset=UTF-8',
                        success: function (data) {
                            console.log("【APITool】::GET success!! 「" + ApiPath + "」");
                            console.log("-------------- data --------------");
                            $.each(data, function (key, val) {
                                console.log("key:" + key + " ,val:" + val);
                            });
                            console.log("-------------- data End --------------");
                            //me.dispatchEvent(eventName, data);
            }
        }).fail(function () {
            console.log("【APITool】::GET fail!! 「" + ApiPath + "」");
          //  me.dispatchEvent(eventName, null);
        });
    }

  },
  created: function () {
      var self = this;
      self.Active_LoadingView(true);
      self.loadingData();
      
  },
  mounted(){
  }
}
</script>

<style>
.google-map{
    z-index: -2;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    background: #fff;
    position: absolute;
    top:0;
    left: 0;
}
</style>


